*,
*:before,
*:after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
* {
  box-sizing: border-box;
}
a:hover,
a:visited,
a:link,
a:active {
  text-decoration: none;
  color: black;
}
html {
  font-family: 'Noto Sans', sans-serif;
  font-size: 15px;
  line-height: 24px;
  font-style: normal;
  font-weight: 400;
  /* vertical-align:middle*/
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-weight: 400;
}

h1 {
  font-size: 35px;
  line-height: 40px;
}

h2 {
  font-size: 30px;
  line-height: 35px;
}
h3 {
  font-size: 25px;
  line-height: 30px;
}
h4 {
  font-size: 20px;
  line-height: 25px;
}

h5,
h6 {
  font-size: 18px;
  line-height: 22px;
}

ul,
li {
  display: block;
  padding: 0;
  margin: 0;
}
a {
  text-decoration: none;
  color: #222222;
  font-family: "Noto Sans JP", sans-serif;
  font-size: 15px;
  vertical-align: middle;
}
a:hover {
  cursor: pointer;
}
li {
  list-style: none;
}
body {
  min-height: 100vh;
  margin: 0 auto;
  width: 100%;
}

.wrapper{
  width: 100%;
  min-height: 100vh;
  height: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.container{
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  background-color: rgb(255, 255, 255);
}
*::-webkit-scrollbar {
  width: 4px;
  height: 4px;
}
*::-webkit-scrollbar-track {
  border: 2px solid rgba(0, 0, 0, 0.2);
}
*::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.3);
}